<template>
  <div class="icons">
    <swiper :options="swiperOptions">
      <swiper-slide v-for="(page, index) of pages" :key="index">
        <div class="icon" v-for="item of page" :key="item.id">
          <div class="icon-img">
            <img class="icon-img-content" :src="item.imgUrl" />
          </div>
          <p class="icon-desc">{{ item.desc }}</p>
        </div>
      </swiper-slide>
    </swiper>
  </div>
</template>

<script>
export default {
  name: 'HomeIcons',
  props: {
    list: Array,
  },
  data() {
    return {
      swiperOptions: {
        autoplay: false,
      },
    }
  },
  computed: {
    pages() {
      const pages = []
      this.list.forEach((item, index) => {
        const page = Math.floor(index / 8)
        if (!pages[page]) {
          pages[page] = []
        }
        pages[page].push(item)
      })
      return pages
    },
  },
}
</script>

<style lang="stylus" scoped>
@import '~styles/varibles.styl'
@import '~styles/mixins.styl'
.icons >>> .swiper-container
  height 0
  padding-bottom 50%
.icon
  position relative
  float left
  width 25%
  height 0
  padding-bottom 25%
  .icon-img
    position absolute
    top 0.2rem
    left 0.3rem
    right 0
    bottom .44rem
    box-sizing border-box
    padding .1rem
    width 1.3rem
    height 1.3rem
    .icon-img-content
      display block
      margin 0 auto
      height 100%
  .icon-desc
    position absolute
    bottom 0
    left 0
    right 0
    height .44rem
    line-height .44rem
    color $darkTextColor
    text-align center
    overflow hidden
    white-space nowrap
    text-overflow ellipsis
    ellipsis()
</style>



